import * as THREE from 'three'
import { MTLLoader } from 'three/examples/jsm/loaders/MTLLoader' // 
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader' // 
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js'
class AGVCar {
    constructor(option) {
        option = option || { scale: 0.1 };
        this.scale = option.scale;
    }
    Load = function (curObj, callBack) {
        let mtlLoader = new MTLLoader();//mtl
        mtlLoader.load('/3DModels/CartoonMan008.mtl', mtl);//.mtlmtl
        function mtl (material) {
            var objLoader = new OBJLoader();//obj
            objLoader.setMaterials(material);//mtlobj
            objLoader.load('/3DModels/CartoonMan008.obj', obj);//.objobj }
            function obj (object3D) {
                object3D.scale.set(1, 1, 1);//object3D
                object3D.traverse(function (child) {
                    if (child instanceof THREE.Mesh) {
                        child.geometry.applyMatrix(new THREE.Matrix4().makeRotationY(Math.PI / 2));
                        child.material.transparent = true;
                    }
                });
                curObj[callBack](object3D);
            }
        }
    }
    LoadFBX = function (curObj, callBack) {
        let fbxLoader = new FBXLoader();//mtl
        fbxLoader.load('/3DModels/Standard Idle.fbx', mtl);//.mtlmtl
        function mtl (material) {
            material.scale.set(0.2, 0.2, 0.2);//object3D
            material.traverse(function (child) {
                if (child instanceof THREE.Mesh) {
                    child.geometry.applyMatrix(new THREE.Matrix4().makeRotationY(Math.PI / 2));
                    child.material.transparent = true;
                }
            });
            curObj[callBack](material);
        }
    }
}
export default AGVCar
